.field-group-items > li > .collapse-field-line,
.field-group-items > li > .remove-field-line {
  opacity: 0;
  background-color: #fff;
  border-radius: 50% !important;
  display: block;
  height: 30px;
  position: absolute;
  left: -15px;
  top: -17px;
  width: 30px;
  transition: all 0.15s ease-in-out 0s;
  transform: rotate(45deg);
  z-index: 2;
  border: 1px solid #ccc; }
  .field-group-items > li > .collapse-field-line span,
  .field-group-items > li > .remove-field-line span {
    width: 20px;
    height: 1px;
    background-color: #000000;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
    .field-group-items > li > .collapse-field-line span:before,
    .field-group-items > li > .remove-field-line span:before {
      content: '';
      display: block;
